:import('Styles/palette.css') {
  i__const_clearBlue_168: clearBlue;
  i__const_pinkRed_169: pinkRed;
  i__const_dark_170: dark;
  i__const_whiteColor_171: whiteColor;
  i__const_neutralColorLight_172: neutralColorLight;
  i__const_neutralColorMedium_173: neutralColorMedium;
}

 :export {
  clearBlue: i__const_clearBlue_168;
  pinkRed: i__const_pinkRed_169;
  dark: i__const_dark_170;
  whiteColor: i__const_whiteColor_171;
  neutralColorLight: i__const_neutralColorLight_172;
  neutralColorMedium: i__const_neutralColorMedium_173;
  cardShadow: 0px 1px 2px rgba(0, 0, 0, .5);
  leaveModalTransition: 200ms;
  navbarHeight: 49.6px;
  navbarHeight: 3.1rem;
  headerHeight: 70.4px;
  headerHeight: 4.4rem;
}

