 .container {
   display: block;
   padding: 20 8%;
 }

 .smCard {
   max-width: 250px;
 }

 .mdCard {
   max-width: 500px;
 }

 :global(.nav) {
   text-decoration: none;
   color: i__const_dark_170;
   padding: 0 10;
   font-weight: bold;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
 }

 :global(.leaveModal) {
   opacity: 0.01;
   -webkit-transition: 200ms;
   transition: 200ms;
 }

 :global(.active-nav) {
   color: i__const_clearBlue_168;
   border-bottom: 3px solid;
 }

 :global(.truncateText) {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}



