:import('Styles/palette.css') {
  i__const_successAccent_126: successAccent;
  i__const_successNeutral_127: successNeutral;
  i__const_successLight_128: successLight;
  i__const_infoAccent_129: infoAccent;
  i__const_infoNeutral_130: infoNeutral;
  i__const_infoLight_131: infoLight;
  i__const_warningAccent_132: warningAccent;
  i__const_warningNeutral_133: warningNeutral;
  i__const_warningLight_134: warningLight;
  i__const_dangerAccent_135: dangerAccent;
  i__const_dangerNeutral_136: dangerNeutral;
  i__const_dangerLight_137: dangerLight;
  i__const_neutralColorMedium_138: neutralColorMedium;
  i__const_neutralColorLight_139: neutralColorLight;
}


:export {
  successAccent: i__const_successAccent_126;
  successNeutral: i__const_successNeutral_127;
  successLight: i__const_successLight_128;
  infoAccent: i__const_infoAccent_129;
  infoNeutral: i__const_infoNeutral_130;
  infoLight: i__const_infoLight_131;
  warningAccent: i__const_warningAccent_132;
  warningNeutral: i__const_warningNeutral_133;
  warningLight: i__const_warningLight_134;
  dangerAccent: i__const_dangerAccent_135;
  dangerNeutral: i__const_dangerNeutral_136;
  dangerLight: i__const_dangerLight_137;
  neutralColorMedium: i__const_neutralColorMedium_138;
  neutralColorLight: i__const_neutralColorLight_139;
}


.notification {
  padding: 20px 20px 5px 20px;
  background-color: i__const_neutralColorLight_139;
  border: 1px solid;
  border-color: i__const_neutralColorMedium_138;
  border-radius: 2px;
}


.footer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.action{
  padding: 5px;
  cursor: pointer;
}

.success{
  color: i__const_successAccent_126;
  background-color: i__const_successNeutral_127;
  border-color: i__const_successLight_128;
}

.info{
  color: i__const_infoAccent_129;
  background-color: i__const_infoNeutral_130;
  border-color: i__const_infoLight_131;
}

.warning{
  color: i__const_warningAccent_132;
  background-color: i__const_warningNeutral_133;
  border-color: i__const_warningLight_134;
}

.danger{
  color: i__const_dangerAccent_135;
  background-color: i__const_dangerNeutral_136;
  border-color: i__const_dangerLight_137;
}


