:import("Styles/palette.css") {
  i__const_whiteColor_87: whiteColor;
  i__const_primaryColor_88: primaryColor;
  i__const_silverColor_89: silverColor;
}

:export {
  colors: "Styles/palette.css";
  whiteColor: i__const_whiteColor_87;
  primaryColor: i__const_primaryColor_88;
  silverColor: i__const_silverColor_89;
}

.container {
  height: 100%;
  padding: 10px;
  position: relative;
}

.imageContainer {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: i__const_silverColor_89;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.fade {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.dotsContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  position: absolute;
  top: 20.8px;
  top: 1.3rem;
  right: 24px;
  right: 1.5rem;
  z-index: 1;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid i__const_whiteColor_87;
  margin-left: 3px;
  cursor: pointer;
}

.isActive {
  background: i__const_primaryColor_88;
  border-color: i__const_primaryColor_88;
}

.footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  padding-bottom: 20px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(#000000));
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}

@-webkit-keyframes fade {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}



