.container {
  position: relative;
  min-width: 250px;
}

.cover {
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: local;
  background-size: cover;
  min-height: 250px;
}

.status {
  position: absolute;
  top: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 25px;
  margin: 10px;
  padding: 0px 15px;
  border-radius: 20px;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 12px;
}

.status::hover{
  opacity: 0.9;
}

.cover .logo {
  width: 90px;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top:50%;
  left: 50%;
}

.address {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 0 15px 10px;
}

.coupons {
  position: absolute;
  bottom: 0;
  right: 0;
  text-align: center;
  padding: 10px;
}

.backdrop::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.4;
}



