:import('Styles/palette.css') {
  i__const_darkColor_115: darkColor;
  i__const_secondaryColor_116: secondaryColor;
  i__const_highlightColor_117: highlightColor;
  i__const_neutralColorMedium_118: neutralColorMedium;
  i__const_whiteColor_119: whiteColor;
  i__const_pinkRed_120: pinkRed;
}

:export {
  colors: 'Styles/palette.css';
  darkColor: i__const_darkColor_115;
  secondaryColor: i__const_secondaryColor_116;
  highlightColor: i__const_highlightColor_117;
  neutralColorMedium: i__const_neutralColorMedium_118;
  whiteColor: i__const_whiteColor_119;
  pinkRed: i__const_pinkRed_120;
}

.title, .subtitle, .header, .p {
  margin: 0;
}

.p {
  color: i__const_darkColor_115;
}

.label {
  opacity: 0.5;
  font-size: 14px;
  margin-bottom: 2px;
}

.highlight {
  color: i__const_highlightColor_117;
}

.secondary {
  color: i__const_secondaryColor_116;
}

.disabled {
  color: i__const_neutralColorMedium_118;
  cursor: not-allowed;
}

.inverted {
  color: i__const_whiteColor_119;
}

.smallText {
  font-size: 13px;
}

.lead {
  font-size: 18px;
}

.normalText {
  font-weight: normal;
  font-family: 'Lato-Regular';
}

.normalFont {
  font-weight: normal;
  font-family: 'FaktPro-Normal';
}

.bold {
  font-weight: bold;
  font-family: 'FaktPro-Semibold';
}

.light {
  font-weight: 300;
  font-family: 'FaktPro-Thin';
}

.error {
  color: i__const_pinkRed_120;
}

a {
  font-family: 'FaktPro-Normal';
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'FaktPro-Normal';
}



