:import("Styles/palette.css") {
  i__const_paleGray_109: paleGray;
  i__const_lightNeutral_110: lightNeutral;
  i__const_pinkRed_111: pinkRed;
  i__const_whiteColor_112: whiteColor
}
:export {
  colors: "Styles/palette.css";
  paleGray: i__const_paleGray_109;
  lightNeutral: i__const_lightNeutral_110;
  pinkRed: i__const_pinkRed_111;
  whiteColor: i__const_whiteColor_112
}
.table {
  width: 100%;
  border-spacing: 0
}
.table thead {
  text-align: left;
  background: i__const_pinkRed_111
}
.table thead tr, .table thead th {
  color: i__const_whiteColor_112
}
.table thead tr p, .table thead tr h1, .table thead tr h2, .table thead tr h3, .table thead tr h5, .table thead tr h6, .table thead tr span, .table thead th p, .table thead th h1, .table thead th h2, .table thead th h3, .table thead th h5, .table thead th h6, .table thead th span {
  color: i__const_whiteColor_112;
}
.table th, .table td {
  padding: 5px 0 5px 10px;
}
.table tbody tr {
  padding: 5px 0
}
.table tbody tr:nth-child(odd) {
  background-color: i__const_lightNeutral_110;
}
.table tbody tr:hover {
  background-color: i__const_paleGray_109;
}



