:import("Styles/palette.css") {
  i__const_whiteColor_84: whiteColor;
  i__const_secondaryColor_85: secondaryColor;
  i__const_lightNeutral_86: lightNeutral;
}

:export {
  colors: "Styles/palette.css";
  whiteColor: i__const_whiteColor_84;
  secondaryColor: i__const_secondaryColor_85;
  lightNeutral: i__const_lightNeutral_86;
}

.container {
  width: 100%;
  background-color: i__const_whiteColor_84;
  max-height: 160px;
  max-height: 10rem;
  overflow-y: scroll;
  text-align: left;
}

.option {
  padding: 5px 16px;
  padding: 5px 1rem;
  cursor: pointer
}

.option.itemSelected {
  background: i__const_secondaryColor_85 !important;
}

.option.itemSelected p {
  color: i__const_whiteColor_84 !important;
}

.option:nth-child(even) {
  background: i__const_lightNeutral_86;
}

.option:hover {
  background-color: i__const_secondaryColor_85;
}

.option:hover p {
  color: i__const_whiteColor_84 !important;
}



