:import('Styles/palette.css') {
  i__const_whiteColor_155: whiteColor;
  i__const_starGradient_156: starGradient;
  i__const_endGradient_157: endGradient;
  i__const_neutralColorMedium_158: neutralColorMedium;
  i__const_mediumNeutral_159: mediumNeutral;
  i__const_lightNeutral_160: lightNeutral;
  i__const_highlightColor_161: highlightColor;
}

:export {
  whiteColor: i__const_whiteColor_155;
  starGradient: i__const_starGradient_156;
  endGradient: i__const_endGradient_157;
  neutralColorMedium: i__const_neutralColorMedium_158;
  mediumNeutral: i__const_mediumNeutral_159;
  lightNeutral: i__const_lightNeutral_160;
  highlightColor: i__const_highlightColor_161;
}

.btn {
  width: 100%;
  min-width: 88px;
  height: 40px;
  outline: none;
  padding: 0 24px;
  padding: 0 1.5rem;
  border-radius: 2.5px;
  color: i__const_whiteColor_155;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
  border: none;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  font-size: 12px;
  font-family: 'FaktPro-Normal';
}

.btn:hover{
   opacity: 0.8;
 }

.small {
  font-size: 10px;
  width: 15%;
}
.medium {
  font-size: 15px;
  width: 35%;
}
.large {
  font-size: 20px;
  width: 60%;
}

.pill {
  border-radius: 20px;
}

.leftIcon {
  margin-right: 10px;
}

.rightIcon {
  margin-left: 10px;
}

.gradient {
  background-image: linear-gradient(60deg, i__const_starGradient_156, 20%, i__const_endGradient_157);
}

.neutral {
  background-color: i__const_lightNeutral_160;
  color: i__const_highlightColor_161
}

.neutral:hover {
  background-color: i__const_mediumNeutral_159;
}

.disabled {
  background-color: i__const_neutralColorMedium_158;
  pointer-events: none;
}



