:import('Styles/palette.css') {
  i__const_neutralColorMedium_121: neutralColorMedium;
}

:export {
  neutralColorMedium: i__const_neutralColorMedium_121;
}

.container {
  display: inline-block;
  position: relative;
}

.inputField {
  display: none;
}

.triggerContainer {
  cursor: pointer
}

.triggerContainer:hover {
  opacity: 0.9;
}

.triggerContainer:hover .uploadIcon {
  opacity: 1;
}

.uploadIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: i__const_neutralColorMedium_121;
  opacity: 0;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.loader{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


