:import('Styles/palette.css') {
  i__const_darkColor_113: darkColor;
  i__const_whiteColor_114: whiteColor
}

:export {
  darkColor: i__const_darkColor_113;
  whiteColor: i__const_whiteColor_114;
  arrowSize: 7px;
  transitionSize: 5px
}

.isShowing {
  opacity: 1 !important;
  visibility: visible !important
}

.isShowing.bottom {
  bottom: 0;
}

.isShowing.top {
  top: 0;
}

.isShowing.left {
  left: 0;
}

.isShowing.right {
  right: 0;
}

.base {
  position: absolute;
  background: i__const_darkColor_113;
  padding: 9.6px;
  padding: 0.6rem;
  border-radius: 0.3rem;
  color: i__const_whiteColor_114;
  display: inline;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  z-index: 1;
  -webkit-transition: visibility 0s ease-in-out 0.1s, opacity 0.2s ease-in-out, all 0.1s ease-in-out;
  transition: visibility 0s ease-in-out 0.1s, opacity 0.2s ease-in-out, all 0.1s ease-in-out;
  min-width: 80px;
  text-align: center;
}

.container {
  position: relative;
}

.top {
  composes: base;
  left: 50%;
  top: 5px;
  -webkit-transform: translate(-50%, -110%);
          transform: translate(-50%, -110%)
}

.top:before {
  content: '';
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 7px solid #394154;
  position: absolute;
  bottom: -5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.right {
  composes: base;
  bottom: 50%;
  right: 5px;
  -webkit-transform: translate(110%, 50%);
          transform: translate(110%, 50%)
}

.right:before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 7px solid i__const_darkColor_113;
  top: 50%;
  left: -5px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.left {
  composes: base;
  bottom: 50%;
  left: 5px;
  -webkit-transform: translate(-110%, 50%);
          transform: translate(-110%, 50%)
}

.left:before {
  content: '';
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid i__const_darkColor_113;
  position: absolute;
  top: 50%;
  right: -5px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.bottom {
  composes: base;
  bottom: 5px;
  left: 50%;
  -webkit-transform: translate(-50%, 120%);
          transform: translate(-50%, 120%)
}

.bottom:before {
  content: '';
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid i__const_darkColor_113;
  position: absolute;
  top: -5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}


